"use client";

import { useState } from "react";
import { Users, Music, ArrowRight, Search } from "lucide-react";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";

export default function MusicCircles() {
  const [searchInput, setSearchInput] = useState("");
  const [relatedArtists, setRelatedArtists] = useState([]);

  const fetchRelatedArtists = async () => {
    if (!searchInput) return;

    const response = await fetch(`/api/get-related-artists?artist=${encodeURIComponent(searchInput)}`);
    const data = await response.json();

    if (data.relatedArtists) {
      setRelatedArtists(data.relatedArtists);
    }
  };

  return (
    <div>
      {/* Search Bar */}
      <div className="flex items-center gap-2 mb-6">
        <input
          type="text"
          value={searchInput}
          onChange={(e) => setSearchInput(e.target.value)}
          placeholder="Enter an artist..."
          className="w-full p-2 border rounded-md"
        />
        <Button onClick={fetchRelatedArtists}>
          <Search className="h-5 w-5 mr-2" />
          Discover
        </Button>
      </div>

      {/* Music Circles */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {relatedArtists.length > 0 ? (
          relatedArtists.map((artist) => (
            <Card key={artist.id} className="overflow-hidden transition-all hover:shadow-lg">
              <CardHeader>
                <CardTitle className="text-lg">{artist.name}</CardTitle>
                <CardDescription className="mt-1">Genre: {artist.genre || "Unknown"}</CardDescription>
              </CardHeader>
              <CardContent>
                <img src={artist.image} alt={artist.name} className="w-full h-40 object-cover rounded-md mb-4" />
                <div className="flex items-center gap-2 mb-4">
                  <Music className="h-4 w-4 text-purple-600" />
                  <span className="text-sm font-medium">{artist.genre}</span>
                </div>
              </CardContent>
              <CardFooter>
                <Button className="w-full">
                  <ArrowRight className="ml-2 h-4 w-4" />
                </Button>
              </CardFooter>
            </Card>
          ))
        ) : (
          <p className="text-gray-500 text-center col-span-3">Search for an artist to discover related music!</p>
        )}
      </div>
    </div>
  );
}
